import style from './Departments.module.scss';
import advice from '../../assets/icons/contract.png';
import ecommerce from '../../assets/icons/e-commerce.svg';
import accounting from '../../assets/icons/accounting.svg';
import Department from './Department/Department';

const Departments = () => {

    const departments = [
        {
            id: 1,
            img: advice,
            type: 'Doradztwo dla maych firm',
            do1: 'Budowanie zdrowego fundamentu dla Twojej firmy',
            do2: 'Zarzdzanie wzrostem, planowanie cieki rozwoju',
        },
        {
            id: 2,
            img: ecommerce,
            type: 'Doradztwo e-commerce',
            do1: 'Tworzenie analiz i planw rynkowych',
            do2: 'Rozszerzenie sprzeday na innych platformach',
        },
        {
            id: 3,
            img: accounting,
            type: 'Ksigowo wewntrzna',
            do1: 'Obsuga kont firmowych',
            do2: 'Usprawnienie procesw ksigowych',
        },
    ];

    return (
        <>
            <div className={style.departmentsContainer}>
                <h2 className={style.departmentsContainer__h2}>Czym si zajmujemy</h2>
                <div className={style.departmentsContainer__wrapper}>

                {departments.map((item) => 
                    <Department 
                    key={item.id}
                    img={item.img}
                    type={item.type}
                    do1={item.do1}
                    do2={item.do2}
                    />
                )}
                </div>
            </div>
        </>
    )
}

export default Departments;